.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  background-color: #e5e5e5;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.card {
  box-sizing: border-box;
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 0;
  padding: 32px 16px 16px;
  display: flex;
}

.card__photo {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__estimate {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  margin: 0 0 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__price-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__buy-button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 0;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 2px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
